<template>
  <div class="page-leons">
    <div class="banner center-content">
      <h2 class="text-center">
        <span class="mb-3 d-block">Leon's</span>
        <span>{{ $t("main.fengshui") }}</span>
      </h2>
    </div>

    <div class="packages-container">
      <b-row class="p-4 p-md-0 justify-content-center">
        <b-col class="mb-4 mb-md-0" v-for="{ acf, id } in getPackages" :key="id">
          <div class="package-card text-center">
            <h5 class="mb-5">
              {{ acf.package_content.services }}
            </h5>
            <p class="mb-5">
              <span class="price mr-3"
                ><sup>$</sup>{{ acf.package_content.price }}</span
              >
              <span> /{{ acf.package_content.unit }}</span>
            </p>
            <p
              v-html="acf.package_content.description"
              class="mb-3"
              style="font-size: 1.2rem"
            ></p>
            <p class="mb-0" v-html="acf.package_content.note"></p>
          </div>
        </b-col>
      </b-row>
    </div>

    <h1 class="leos-slogon" v-html="$t('main.fsSlogon')"></h1>

    <router-link :to="{ name: 'Home' }">
      <div class="back-btn">返回</div>
    </router-link>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'leonsSpace',
  data () {
    return {
      locale: ''
    }
  },
  computed: {
    ...mapGetters(['allPackages']),

    getPackages () {
      const ProperPackage = {}

      this.renameLocale()
      let key = 0

      this.allPackages.forEach((element, i) => {
        if (
          element.polylang_current_lang === this.locale &&
          element.acf.package_content
        ) {
          ProperPackage[key] = element
          key++
        }
      })
      return ProperPackage
    }
  },
  methods: {
    renameLocale () {
      this.$i18n.locale === 'en'
        ? (this.locale = 'en_US')
        : (this.locale = 'zh_TW')
    }
  }
}
</script>

<style lang="scss" scoped>
.page-leons {
  background: #fff;
  color: #363636;
  overflow: hidden;
  min-height: 100vh;
  width: 100%;
  .leos-slogon {
    font-size: 24px;
    font-weight: 500;
    max-width: 700px;
    padding: 0 22px;
    margin-bottom: 100px;
    margin-left: auto;
    text-align: center;
    margin-right: auto;
  }
  h1,
  h2 {
    color: #363636;
  }
  .banner {
    height: 400px;
    background: $gray;
    h2 {
      color: $text;
      span:nth-child(1) {
        font-size: 1.5rem;
      }
      span:nth-child(2) {
        font-size: 3rem;
        letter-spacing: 1.5rem;
        padding-left: 1.5rem;
      }
    }
  }
  .packages-container {
    max-width: 1200px;
    margin: 100px auto;

    .col {
      width: 100%;
      min-width: 100%;
    }

    .package-card {
      height: 100%;
      background: #f1f2f4;
      padding: 50px 22px;

      .price {
        font-size: 2.5rem;
        font-weight: bold;
      }
    }
  }
  @media (min-width: 768px) {
    .packages-container {
      .col {
        min-width: calc(100% / 3);
        max-width: calc(100% / 3);
      }
    }
  }
}
</style>
